<script type="text/ecmascript-6">
  import NavHeader from '../../bese/navheader/navheader.vue'
  import NavFooter from '../../bese/navfooter/navfooter.vue'
  import Loading from '../../bese/loading/loading.vue'
  import {swiper, swiperSlide} from 'vue-awesome-swiper'
  require('swiper/dist/css/swiper.css');
  import axios from 'axios'

  export default {
    name: 'index',
    components: {
      NavHeader,
      NavFooter,
      swiper,
      swiperSlide,
      Loading
    },
    data() {
      return {
        imgs: [],
        nickName:[],
        headerLines:[],
        recommend: [],
        KStimeList: [],
        hotSell:[],
        weekHotSell:[],
        chujian:[],
        YCJX:[],
        jx1: [],
        jx2: [],
        jx3: [],
        isJx1:false,
        isJx2:false,
        isJx3:true,
        bolus:[],
        chichens:[],
        cows:[],
        crad:[],
        ducks:[],
        fishs:[],
        noodles:[],
        sheeps:[],
        shrimps:[],
        pigs:[],
        seasonings:[],
        num: 0,
        swiperOption: {
          direction:"horizontal",
          loop:true,
          pagination:".swiper-pagination",
          prevButton:".swiper-button-prev",
          nextButton:".swiper-button-next",
          autoplay:3000,
          autoplayDisableOnInteraction: false,
        },
        swiperOption1: {
          notNextTick: true,
          autoplay: 3000,
          loop: true,
          autoplayDisableOnInteraction: false,
          direction: 'horizontal',
          effect: 'coverflow',
          grabCursor: true,
          setWrapperSize: true,
          autoHeight: true,
          paginationType: 'bullets',
          pagination: '.swiper-pagination',
          paginationClickable: true,
          scrollbar: '.swiper-scrollbar',
          mousewheelControl: true,
          observeParents: true,
        },
        swiperOption2: {
          direction:"horizontal",
          loop:true,
          pagination:".swiper-pagination",
          prevButton:".swiper-button-prev",
          nextButton:".swiper-button-next",
          autoplay:3000,
          effect:"cube",
          slidesPerView:3,
          centeredSlides:true,
          coverflow:{
            rotate:30,
            stretch:10,
            depth:60,
            modifier:2,
            slideshadows:true
          }
        },
        hour1:"0",
        hour2:"1",
        minute1: '0',
        minute2: '0',
        second1: '0',
        second2: '0',
      }
    },
    computed: {
      swiper() {
        return this.$refs.mySwiper.swiper
      }
    },
    mounted() {
      this.showUp();
      this.showSearch();
      this.bianse();
      this.changeHeaderLine();
    },
    created() {
      this.$root.Hub.$on('userName', () => {
        this.nickName= 'this.nickName';
        console.log(this.nickName)
      });
      setTimeout(() => {
        this._getBanner();
      }, 4000);
      this._getheaderLine();
      this._getrecommend();
      this._getjx();
      this._getKsTime();
      this._putTimeList();
      this._getYCJX();
      this._getWeekHotSell();
      this._getchujian();
      this._getGoodsList();
    },
    methods: {
      _getBanner() {
        axios.get('/api/banner').then((res) => {
          this.imgs = res.data;
        })
      },
      _getheaderLine() {
        axios.get('/api/headerLine').then((res) => {
          this.headerLines = res.data;
        })
      },
      _getrecommend() {
        axios.get('api/recommend').then((res) => {
          this.recommend = res.data;
        })
      },
      _getjx() {
        axios.get('api/jx').then((res) => {
          this.jx1 = res.data[0].list1;
          this.jx2 = res.data[1].list2;
          this.jx3 = res.data[2].list3;
        })
      },
      _getKsTime() {
        let times = 2 * 60 * 60;
        let timer = setInterval((() => {
          times--;
          let h = Math.floor(times / (60 * 60));
          let m = Math.floor(times / 60 % 60);
          let s = times % 60;
       /*   this.hour1 = h > 10 ? Math.floor(h / 10) : 0;
          this.hour2 = h % 10;
          this.minute1 = m > 10 ? Math.floor(m / 10) : 0;
          this.minute2 = m % 10;
          this.second1 = s > 10 ? Math.floor(s / 10) : 0;
          this.second2 = s % 10;*/
          if (this.minute1 === 0 && this.minute2 === 0) {
            clearInterval(timer);
          }
        }), 1000);

      },
      _getYCJX(){
        axios.get('api/tuijian').then((res) => {
          this.YCJX = res.data;
        })
      },
      _getWeekHotSell(){
        axios.get('api/hotSell').then((res) => {
          this.weekHotSell = res.data;
        })
      },
      _getchujian(){
        axios.get('api/chujian').then((res) => {
          this.chujian = res.data;
        })
      },
      _putTimeList() {
        let List = [
          {
            date: "",
            time: "10:00"
          },
          {
            date: "",
            time: "15:00"
          },
          {
            date: "",
            time: "20:00"
          },
        ];
        let nowDate = new Date().toLocaleDateString();
        let timo = List.filter((p) => {
          return p.time == "10:00";
        });
        timo[0].date = nowDate;
        let timo1 = List.filter((p) => {
          return p.time == "15:00";
        });
        timo1[0].date = nowDate;
        let timo2 = List.filter((p) => {
          return p.time == "20:00";
        });
        timo2[0].date = nowDate;
        this.KStimeList = List;
      },
      _getGoodsList() {
        axios.get('api/goodsList').then((res) => {
            this.crad = res.data[0].goodsImg,
            this.shrimps = res.data[7].goodsImg,
            this.fishs = res.data[6].goodsImg,
            this.pigs = res.data[4].goodsImg,
            this.cows = res.data[3].goodsImg,
            this.sheeps = res.data[5].goodsImg,
            this.ducks = res.data[2].goodsImg,
            this.chichens = res.data[1].goodsImg,
            this.bolus = res.data[8].goodsImg,
            this.noodles = res.data[10].goodsImg,
            this.seasonings = res.data[9].goodsImg
        })
      },
      changeHeaderLine() {
        let headerline = this.$refs.headerline;
        let headerlineD = this.$refs.headerlineD;
        let a = 0;
        setInterval(()=> {
          a++;
          if (a == 3){
            a =0
          }
          let l = -20*a;
          headerlineD.style.top = l+"px";
        },3000)

      },
      showUp() {
        let height = this.$refs.goodGoods.offsetHeight;
        let upTop = this.$refs.upTop;
        window.addEventListener('scroll', () => {
          let top = window.scrollY;
          if (top >= height) {
            upTop.style.display = "block"
          }
          else if (top < height){
            upTop.style.display = "none"
          }
        });

      },
      upToTop() {
        document.body.scrollTop = 0;
        document.documentElement.scrollTop = 0;
      },
      showSearch(){
        let search      = this.$refs.suosuo;
        let searchHight = this.$refs.suosuo.offsetHeight;
        window.addEventListener('scroll',()=>{
          let top = window.scrollY;
          if (top > searchHight) {
            search.style.position = "fixed";
            search.style.maxWidth= "640px";
            search.style.width ="100%";
            search.style.top = 0;
            search.style.zIndex=999;
          } else if (top <= searchHight){
            search.style.position = "";
          }
        })
      },
      bianse() {
        let search = this.$refs.suosuo;
        let searchHight = this.$refs.banner.offsetHeight;
        window.addEventListener('scroll', () => {
          let top = window.scrollY;
          if (top > searchHight) {
            search.style.background = "rgba(240,240,240,1)"
          } else {
            let op = (top / searchHight) * 0.85;
            search.style.background = "rgba(240,240,240," + op + ")";
          }

        })

      },
      addCart(index){
        alert("")
      },
      lookGoods(index){
        this.$router.push({path: "/GoodsDetails"})
      },
      intoSearch(){
        this.$router.push({path:"/search"})
      },
      tab(index) {
        this.num = index;
        if (this.num==0){
          this.isJx1=true;
          this.isJx2=false;
          this.isJx3=false;
        }
        else if (this.num==1){
          this.isJx2=true;
          this.isJx1=false;
          this.isJx3=false;
        }
        else if (this.num==2){
          this.isJx3=true;

          this.isJx1=false;
          this.isJx2=false;
        }
      },
      showBanner(index) {
        this.$router.push({path: "/GoodsDetails"})
      },
      showRecommend(index) {
        this.$router.push({path: '/GoodsDetails'})
      },
      showjx1(index) {
        this.$router.push({path: '/GoodsDetails'})
      },
      showjx2(index) {
        this.$router.push({path: '/GoodsDetails'})
      },
      showjx3(index) {
        this.$router.push({path: '/GoodsDetails'})
      },
      showjx4(index){
        this.$router.push({path: '/GoodsDetails'})
      },
      showCJ(index){
        this.$router.push({path: '/GoodsDetails'})
      },
      showRM(index){
        this.$router.push({path: '/GoodsDetails'})
      }
    }
  }
