<template>
  <div id="goodsDetails">
    <navheader></navheader>
    <search-navbar></search-navbar>
    <div class="goodsDetails-top">
      <div class="" v-for="(item,index) in goodsDetailsTop">
        <div
          :class="{'topBottom':index === num}"
          @click="tab(index)"
        >
          {{item.text}}
        </div>
      </div>
    </div>
    <div class="goodsDetails-center">
      <div class="goodsInfo" v-for="(item,index) in  goodsDetailsCenter">
        <div class="goodsInfo-img" v-show="index==0&&index==num">
          <div class="goodsInfo-img-top">
            <div class="top-img">
              <swiper :options="swiperOption" ref="mySwiper">
                <swiper-slide v-for="item in smallImg" :key="item.id">
                  <img :src="item.simg">
                </swiper-slide>
                <div class="swiper-pagination" slot="pagination"></div>
                <div class="swiper-scrollbar" slot="scrollbar"></div>
              </swiper>
            </div>
          </div>
          <div class="goodsInfo-img-center" v-for="item in goodsInfo">
            <div class="center-title">
              <span class="title-text">{{item.title}}</span>
            </div>
            <div class="center-price">
              <span></span>
              <span>:</span>
              <span style="color:firebrick;font-size: 22px">{{item.price}}</span>
              <span></span>
            </div>
            <div class="center-gg">
              <span></span><span>{{item.gg}}</span>
            </div>
            <div class="center-address">
              <span>:</span>
              &nbsp;<span></span>
              &nbsp;<span></span>
              &nbsp;<i class="iconfont icon-dizhi"></i>
            </div>
          </div>
        </div>
        <div class="goodsInfo-ge" v-show="index==1&&index==num">
          <div class="goodsInfo-ge-top">
            <div v-for="(item,index) in goodsInfoGeTop"
                 :class="{'fontColor':index === num1}" @click="tab1(index)">
              {{item.text}}
            </div>
          </div>
          <div class="goodsInfo-ge-bottom"
               v-for="(item,index) in goodsInfoGeBottom">
            <div class="ge-bottom-one" v-show="index==0&&index==num1">
              <div class="ge-bottom-img" v-for="(item,index) in productImg">
                <img :src="item.pimg" alt="" class="">

              </div>
            </div>
            <div class="ge-bottom-two" v-show="index==1&&index==num1">
              <div class="ge-bottom-two-tr" v-for="(item,index) in details">
                <div class="">
                  <span></span>
                  <span>:</span>
                  <span>{{item.pp}}</span>
                </div>
                <div class="">
                  <span></span>
                  <span>:</span>
                  <span>{{item.bzq}}</span>
                </div>
                <div class="">
                  <span></span>
                  <span>:</span>
                  <span>{{item.jhl}}</span>
                </div>
                <div class="">
                  <span></span>
                  <span>:</span>
                  <span>{{item.tj}}</span>
                </div>
              </div>
            </div>
            <div class="ge-bottom-three" v-show="index==2&&index==num1">
              <div class="">
                <p class=""></p>
                <div class="">+1-2+1++</div>
              </div>
              <div class="">
                <p class=""></p>
                <div class="">1<br>
                  48100<br>
                  2<br>
                  YC-fresh<br>
                  400-626-3311<br>
                  09:00-24:00<br>
                  <br>
                  <br>
                </div>
              </div>
              <div class="">
                <p class=""></p>
                <div class="">1<br>2<br>3<br>4<br>
                </div>
              </div>
            </div>
          </div>
        </div>
        <div class="goodsInfo-pl" v-show="index==2&&index==num">
          2
        </div>
      </div>
    </div>
    <div class="goodsDetails-bottom">
      <div class="bottom-kf">
        <i class="iconfont icon-kefu"></i>
        <i class="iconfont icon-heart" @click="collection" ref="Collection"></i>
      </div>
      <div class="bottom-addCart" @click="addCart">
        
      </div>
      <div class="bottom-NowBuy" @click="nowBuy">
        
      </div>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">

  import Navheader from "../../bese/navheader/navheader.vue";
  import SearchNavbar from "../../bese/searchNavbar/seachNavbar.vue";
  import axios from 'axios';
  import {swiper, swiperSlide} from 'vue-awesome-swiper'

  require('swiper/dist/css/swiper.css');

  export default {
    name: 'goodsDetails',
    components: {
      SearchNavbar,
      Navheader,
      swiper,
      swiperSlide,
    },
    data() {
      return {
        goodsDetailsTop: [{text: ""}, {text: ""}, {text: ""}],
        goodsDetailsCenter: [{}, {}, {}],
        goodsInfoGeTop: [{text: ""}, {text: ""}, {text: ""}],
        goodsInfoGeBottom: [{}, {}, {}],
        num: 0,
        num1: 0,
        goodsInfo: [],
        smallImg: [],
        bigImg: [],
        productImg: [],
        details: [],
        showBottom: [],
        swiperOption: {
          direction: "horizontal",
          loop: true,
          pagination: ".swiper-pagination",
          prevButton: ".swiper-button-prev",
          nextButton: ".swiper-button-next",
          autoplay: 3000,
          autoplayDisableOnInteraction: false,
        },
      }

    },
    computed: {
      swiper() {
        return this.$refs.mySwiper.swiper
      }
    },
    created() {
      this._getGoodsDetails();

    },
    methods: {
      _getGoodsDetails() {
        axios.get('/api/goodsDetails')
          .then((res) => {
            this.goodsInfo = res.data[0].goodsInfo;
            this.smallImg = res.data[0].smallImg;
            this.bigImg = res.data[0].bigImg;
            this.productImg = res.data[0].productImg;
            this.details = res.data[0].details;
            this.showBottom = res.data[0].showBottom;
            console.log(this.smallImg)
          })
          .catch((err) => {
            console.log(err)
          })
      },
      tab(index) {
        this.num = index;
      },
      tab1(index) {
        this.num1 = index;
      },
      collection() {
        if (this.$refs.Collection.className === 'iconfont icon-heart') {
          this.$refs.Collection.className = 'iconfont icon-heart red'
        } else if (this.$refs.Collection.className === 'iconfont icon-heart red') {
          this.$refs.Collection.className = 'iconfont icon-heart '
        }
      },
      addCart() {
        alert('')
      },
      nowBuy() {
        alert('hh')
      }


    },

  }
</script>
<style scoped lang="less" rel="stylesheet/less">
  @import "../../common/less/base";

  .goodsDetails-top {
    height: 50px;
    background-color: @color-F0;
    display: flex;
    > div {
      flex: 1;
      display: flex;
      align-items: center;
      justify-content: center;
      > div {
        height: 100%;
        width: 50%;
        display: flex;
        align-items: center;
        justify-content: center;
        font-size: @font-size-large;
        border: 1px solid @color-F0;
      }
    }
    .topBottom {
      border-bottom: 1px solid @color-red;
    }

  }

  .goodsDetails-center {
    .goodsInfo {
      .goodsInfo-img {
        height: 620px;
        .goodsInfo-img-top {
          height: 380px;
          .top-img {
            img {
              width: 100%;
            }
          }
        }
        .goodsInfo-img-center {
          height: 200px;
          .center-title {
            height: 50px;
            display: flex;
            align-items: center;
            justify-content: center;
            font-size: @font-size-medium-x;
          }
          .center-price {
            height: 50px;
            display: flex;
            align-items: center;
            padding-left: 20px;
            font-size: @font-size-medium-x;

          }
          .center-gg {
            height: 50px;
            display: flex;
            align-items: center;
            padding-left: 20px;
            font-size: @font-size-medium-x;

          }
          .center-address {
            height: 50px;
            display: flex;
            align-items: center;
            padding-left: 20px;
            font-size: @font-size-medium;
            .icon-dizhi {
              font-size: @font-size-large-x;
              color: @color-red;
            }
          }
        }
      }
      .goodsInfo-ge {
        .goodsInfo-ge-top {
          display: flex;
          align-items: center;
          div {
            height: 40px;
            border-top: 3px solid @color-white;
            border-bottom: 1px solid @color-white;
            flex: 1;

            display: flex;
            background-color: @color-yellow;
            align-items: center;
            justify-content: center;

          }
          .fontColor {
            color: @color-red;
          }
        }
        .goodsInfo-ge-bottom {
          .ge-bottom-one {
            .ge-bottom-img {
              width: 95%;
              margin: 10px auto 60px auto;
              img {
                width: 100%;
              }
            }
          }
          .ge-bottom-two {
            .ge-bottom-two-tr {
              width: 95%;
              margin: 20px auto;
              border-right: 1px solid @color-F0;
              border-left: 1px solid @color-F0;
              div {
                display: flex;
                margin: 10px auto;
                border: 1px solid @color-F0;
                span:first-child {
                  flex: 5;
                  display: flex;
                  align-items: center;
                  justify-content: center;
                }
                span:nth-child(1) {
                  flex: 1;
                  display: flex;
                  align-items: center;
                  justify-content: center;
                }
                span:last-child {
                  flex: 5;
                  display: flex;
                  align-items: center;
                  justify-content: center;
                }
              }

            }

          }
          .ge-bottom-three {
            margin-bottom: 60px;
            div {
              width: 95%;
              margin: 10px auto;
              background-color: @color-F0;
            }
          }

        }
      }
    }
  }

  .goodsDetails-bottom {
    max-width: 640px;
    width: 100%;
    height: 50px;
    margin: 0 auto;
    background-color: @color-F0;
    position: fixed;
    bottom: 0;
    z-index: 99;
    display: flex;
    .bottom-kf {
      flex: 3;
      display: flex;
      align-items: center;
      i {
        flex: 1;
        display: flex;
        align-items: center;
        justify-content: center;
      }
      .icon-kefu {
        font-size: @font-size-large;
        color: @color-dialog-background;
      }
      .icon-heart {
        font-size: @font-size-large;
        color: @color-dialog-background;
      }
      .red {
        color: @color-red;
      }

    }
    .bottom-addCart {
      flex: 2;
      display: flex;
      align-items: center;
      justify-content: center;
      background-color: @color-theme;
      color: @color-white;
      font-size: @font-size-medium-x;
    }
    .bottom-NowBuy {
      flex: 2;
      display: flex;
      align-items: center;
      justify-content: center;
      background-color: @color-red;
      color: @color-white;
      font-size: @font-size-medium-x;
    }
  }


</style>



